@import "../../styles/abstracts/index.scss";

.navigation {
  display: flex;
  margin-bottom: 40px;
  justify-content: flex-start;
  align-items: center;
  height: 48px;
  color: #ffffff;
  font-size: 14px;

  &__list {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    align-items: center;
  }

  &__item:last-child {
    color: $color-secondary;
    width: 155px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    @include onTabletSize {
      width: 300px;
    }

    @include onDesktop {
      width: 300px;
    }
  }

  &__item:nth-child(1):not(:last-child)::after,
  &__item:nth-child(2):not(:last-child)::after {
    content: "";

    display: inline-block;
    background-image: url(../../assets/icons/chevron-right-grey.svg);
    width: 6px;
    height: 9px;

    margin-right: 13px;
  }

  &__link {
    color: inherit;
    text-decoration: none;
    margin-right: 13px;
  }

  &__link--home {
    vertical-align: middle;
  }
}
